<template>
  <h2 class="text-center my-3 p-2">後台產品列表</h2>
  <div class="container">
    <div class="text-end mt-4">
      <button class="btn btn-primary" type="button" @click="openModal('isNew')">
        <!--帶值進去isNew 代表是新的一筆-->
        <!--標記建立新的產品-->
        建立新的產品
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr class="py-3">
          <th width="120">分類</th>
          <th>產品名稱</th>
          <th width="120">原價</th>
          <th width="120">售價</th>
          <th width="100">是否啟用</th>
          <th width="120">編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-end"></td>
          <td class="text-end">
            {{ item.price }}
          </td>
          <td>
            <span class="text-success" v-if="item.is_enabled">啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td>
            <div class="btn-group">
              <button
                type="button"
                class="btn btn-outline-primary btn-sm"
                @click="openModal('edit', item)"
              >
                <!--帶值進去edit 代表編輯，後面item是當前一筆的資料-->
                編輯
              </button>
              <button
                type="button"
                class="btn btn-outline-danger btn-sm"
                @click="openModal('delete', item)"
              >
                <!--帶值進去delete 代表編輯，後面item是當前一筆的資料-->
                刪除
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <!--內層:pages, 外層:pagination -->
    <!--內層:get-product, 外層getProducts-->
    <pagination :pages="pagination" @get-product="getProducts"></pagination
    ><!--分頁功能-->
  </div>
</template>
